import React from 'react';
import './PageStyles.css';
import { color, motion } from 'framer-motion';

const pageTransition = {
  initial: { opacity: 0, y: 40 },
  animate: { opacity: 1, y: 0 },
  exit: { opacity: 0, y: -40 },
  transition: { duration: 0.6, ease: 'easeInOut' },
};

const UeberMich = () => (
  <motion.div
    className="page uebermich-page"
    initial={pageTransition.initial}
    animate={pageTransition.animate}
    exit={pageTransition.exit}
    transition={pageTransition.transition}
  >
  <div className='aboutmetext'>
    <h1>ber Mich</h1>
    <h2>Pia Reitz - Pia Ingrid vom Planbach </h2>
    <p>Nhen begleitet mich schon mein ganzes Leben. Alles begann um 1980, als ich von meiner Mutter ihre alte Paff-Nhmaschine erbte. Mit viel Neugier und Kreativitt fertigte ich damals aus alten Jeans und Leder die sogenannten "Ibiza-Jacken" - ein echtes Upcycling-Projekt, lange bevor der Begriff in Mode kam.<br/><br/> Kurz darauf gnnte ich mir meine erste eigene neue Pfaff-Maschine, eine Tiptronic. Etwa zu dieser Zeit entdeckte ich gemeinsam mit meinem Mann das Westernhobby. Wir tauchten zuerst in die Epoche um 1860 ein, spter verlug es uns in die Zeit um 1740.<br/><br/> Die ersten historischen Schnittmuster - sogenannte "Past Patterns" aus Amerika - musste ich noch mhsam von Hand bersetzzen. Doch genau diese Herausforderung entfachte meine Leidenschaft fr historische Gewandungen.<br/><br/> In den folgenden Jahren habe ich vor allem meine Familie fr unser gemeinsames Hobby ausgestattet, Zelte genht und auch fr Freunde individuelle Kleidungsstcke angefertigt Seit nunmehr 16 Jahren bin ich mit den historischen angelegten Verein die <strong>Freyen Rittersleut zu Randingen</strong>im Mittelalter unterwegs. Meine aktuellen Ngprojekte sind fest in dieser Epoche verwurzelt - mit viel Liebe zum Detail und historischer Genauigkeit.<br/><br/> Seit meinem Eintritt in den Ruhestand habe ich beschlossen, mein Hobby in einen kleinen Beruf zu verwandeln - und so entstand <strong>Pia's Nhstube</strong>. Hier nhe ich nicht nur historische Gewandungen, sondern widme mich auch dem Upcycling moderner Materiealien.<br/><br/> In meiner Galerie findest du Beispiele meine Areiten - von liebevoll restaurierten Einzelstcken bis hin zu kreativen Neuanfertigungen. Auch fr nderungen und Anpassungen bin ich jederzeit offen.<br/><br/> Nhen ist fr mich nicht nur Handwerk - es ist Ausdruck von Geschichte, Persnlichkeit und Nachhaltigkeit. Ich freue mich, wenn ich mit meiner Arbeit ein Stuck dieser Leidenschaft weitergeben kann.</p>
  <h2>Wo Geschichte beginnt</h2>
  <p>Du suchst nach Hochwertigen Stoffen, stilvollen Accessoires oder mchtest dich einfach inspirieren lassen? Ich mchte dir einige meiner liebsten Quellen ans Herz legen - Orte, an denen ich selbst gerne stbere, einkaufe und mich inspirieren lasse. Vielleicht findest du dort genau das Material oder Inspiration, das deinem nchsten Projekt das gewisse etwas verleiht.</p>
  </div>
  <div className='contact-form'><ul className='aboutMe-ref'>
    <li className='contact-form-inside'><a href='https://tuchundstoff.shop/' style={{color: '#7B4019'}}>Stofftrume ~ TuchundStoff.shop</a></li>
      <dd className='listDetail'>Qualittsstoffe fr zeitgenssische Mode und gelebte Geschichte. Ob Leinen, Loden oder Samt - hier findest du Naturmaterialien aus europischen Webereien, ideal fr Reenactment, Maschneiderei oder kreative Projekte.</dd>
    <li className='contact-form-inside'><a href='https://www.die-hutschmiede.de/'style={{color: '#7B4019'}}>Lieblingsstcke ~ Die Hutschmiede</a></li>
      <dd className='listDetail'>Gewandmacherei, Alltagshte und Dekoration mit mittelalterlichem Flair. Sie bieten liebevoll gefertigte Einzelstcke an und gehen individuell auf Kundenwnsche ein - ein echter Geheimtipp fr stilvolle Gewnder und Accessoires.</dd>
    <li className='contact-form-inside'><a href='https://www.freye-rittersleut.net/verein/'style={{color: '#7B4019'}}>Schatzkammer ~ Freye Rittersleut zu Randingen e.V.</a></li>
      <dd className='listDetail'>Ein Mittelalterverein mit Herzblut und Hingabe. Wer sich fr authentische Darstellung, historische Handwerkskunst und lebendige Geschichte interessiert, findet hier nicht nur Inspiration, sonder auch eine lebendige Gemeinschaft, die das Mittelalter auf Mrkten und Burgbelebungen zum Leben erweckt.</dd>
  </ul>
  </div>
  </motion.div>
);

export default UeberMich;
